"use client"

import { useState, useEffect } from 'react'
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog"
import { Skeleton } from "@/components/ui/skeleton"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

const getTypeColor = (type: string): string => {
  const colors: { [key: string]: string } = {
    normal: "bg-gray-400",
    fire: "bg-red-500",
    water: "bg-blue-500",
    electric: "bg-yellow-500",
    grass: "bg-green-500",
    ice: "bg-cyan-400",
    fighting: "bg-red-700",
    poison: "bg-purple-500",
    ground: "bg-yellow-700",
    flying: "bg-indigo-400",
    psychic: "bg-pink-500",
    bug: "bg-lime-500",
    rock: "bg-yellow-800",
    ghost: "bg-purple-700",
    dragon: "bg-indigo-700",
    dark: "bg-gray-700",
    steel: "bg-gray-500",
    fairy: "bg-pink-400",
  }
  return colors[type] || "bg-gray-400"
}

interface Pokemon {
  id: number
  name: string
  url: string
  sprites?: {
    front_default: string
  }
  types?: {
    type: {
      name: string
    }
  }[]
  stats?: {
    base_stat: number
    stat: {
      name: string
    }
  }[]
}

export default function Home() {
  const [pokemons, setPokemons] = useState<Pokemon[]>([])
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedPokemon, setSelectedPokemon] = useState<Pokemon | null>(null)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [totalPages, setTotalPages] = useState(1)
  const [typeFilter, setTypeFilter] = useState("all")
  const [generationFilter, setGenerationFilter] = useState("all")
  const itemsPerPage = 20

  useEffect(() => {
    const fetchPokemons = async () => {
      try {
        setLoading(true)
        // Get total number of Pokémon
        const countResponse = await fetch('https://pokeapi.co/api/v2/pokemon-species/')
        const countData = await countResponse.json()
        const totalPokemon = countData.count
        setTotalPages(Math.ceil(totalPokemon / itemsPerPage))

        // Fetch paginated Pokémon
        const offset = (currentPage - 1) * itemsPerPage
        const response = await fetch(`https://pokeapi.co/api/v2/pokemon?limit=${itemsPerPage}&offset=${offset}`)
        const data = await response.json()
        
        const pokemonDetails = await Promise.all(
          data.results.map(async (pokemon: Pokemon) => {
            const res = await fetch(pokemon.url)
            return res.json()
          })
        )
        
        setPokemons(pokemonDetails)
        setLoading(false)
      } catch (error) {
        console.error('Error fetching pokemon:', error)
        setLoading(false)
      }
    }

    fetchPokemons()
  }, [currentPage])

  const getGeneration = (id: number): string => {
    if (id <= 151) return "1"
    if (id <= 251) return "2"
    if (id <= 386) return "3"
    if (id <= 493) return "4"
    if (id <= 649) return "5"
    if (id <= 721) return "6"
    if (id <= 809) return "7"
    if (id <= 905) return "8"
    return "9"
  }

  const renderPaginationButtons = () => {
    const buttons = []
    const maxVisiblePages = 5
    let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2))
    let endPage = Math.min(totalPages, startPage + maxVisiblePages - 1)

    if (endPage - startPage + 1 < maxVisiblePages) {
      startPage = Math.max(1, endPage - maxVisiblePages + 1)
    }

    // First page
    if (startPage > 1) {
      buttons.push(
        <button
          key={1}
          onClick={() => setCurrentPage(1)}
          className="px-3 py-2 bg-white border rounded-lg hover:bg-gray-50"
        >
          1
        </button>
      )
      if (startPage > 2) {
        buttons.push(<span key="ellipsis1" className="px-2">...</span>)
      }
    }

    // Visible pages
    for (let i = startPage; i <= endPage; i++) {
      buttons.push(
        <button
          key={i}
          onClick={() => setCurrentPage(i)}
          className={`px-3 py-2 border rounded-lg ${
            currentPage === i
              ? 'bg-gray-800 text-white'
              : 'bg-white hover:bg-gray-50'
          }`}
        >
          {i}
        </button>
      )
    }

    // Last page
    if (endPage < totalPages) {
      if (endPage < totalPages - 1) {
        buttons.push(<span key="ellipsis2" className="px-2">...</span>)
      }
      buttons.push(
        <button
          key={totalPages}
          onClick={() => setCurrentPage(totalPages)}
          className="px-3 py-2 bg-white border rounded-lg hover:bg-gray-50"
        >
          {totalPages}
        </button>
      )
    }

    return buttons
  }

  const filteredPokemons = pokemons.filter(pokemon => {
    const matchesName = pokemon.name.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesType = typeFilter === "all" || pokemon.types?.some(type => type.type.name === typeFilter)
    const matchesGeneration = generationFilter === "all" || getGeneration(pokemon.id) === generationFilter
    return matchesName && matchesType && matchesGeneration
  })

  return (
    <main className="min-h-screen p-8 bg-gray-100">
      {/* Hero Section */}
      <div className="text-center mb-12 bg-gradient-to-b from-gray-50 to-white py-8 rounded-lg shadow-sm">
        <h1 className="text-5xl font-bold mb-4 text-gray-800">Pokédex</h1>
        <p className="text-xl text-gray-600 mb-8">Discover all your favorite Pokémon!</p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center items-center mb-8">
          <Input
            type="search"
            placeholder="Search Pokémon..."
            className="max-w-md"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <Select value={typeFilter} onValueChange={setTypeFilter}>
            <SelectTrigger className="w-48">
              <SelectValue placeholder="Filter by type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Types</SelectItem>
              <SelectItem value="normal">Normal</SelectItem>
              <SelectItem value="fire">Fire</SelectItem>
              <SelectItem value="water">Water</SelectItem>
              <SelectItem value="electric">Electric</SelectItem>
              <SelectItem value="grass">Grass</SelectItem>
              <SelectItem value="ice">Ice</SelectItem>
              <SelectItem value="fighting">Fighting</SelectItem>
              <SelectItem value="poison">Poison</SelectItem>
              <SelectItem value="ground">Ground</SelectItem>
              <SelectItem value="flying">Flying</SelectItem>
              <SelectItem value="psychic">Psychic</SelectItem>
              <SelectItem value="bug">Bug</SelectItem>
              <SelectItem value="rock">Rock</SelectItem>
              <SelectItem value="ghost">Ghost</SelectItem>
              <SelectItem value="dragon">Dragon</SelectItem>
              <SelectItem value="dark">Dark</SelectItem>
              <SelectItem value="steel">Steel</SelectItem>
              <SelectItem value="fairy">Fairy</SelectItem>
            </SelectContent>
          </Select>
          <Select value={generationFilter} onValueChange={setGenerationFilter}>
            <SelectTrigger className="w-48">
              <SelectValue placeholder="Filter by generation" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Generations</SelectItem>
              <SelectItem value="1">Generation I (1-151)</SelectItem>
              <SelectItem value="2">Generation II (152-251)</SelectItem>
              <SelectItem value="3">Generation III (252-386)</SelectItem>
              <SelectItem value="4">Generation IV (387-493)</SelectItem>
              <SelectItem value="5">Generation V (494-649)</SelectItem>
              <SelectItem value="6">Generation VI (650-721)</SelectItem>
              <SelectItem value="7">Generation VII (722-809)</SelectItem>
              <SelectItem value="8">Generation VIII (810-905)</SelectItem>
              <SelectItem value="9">Generation IX (906+)</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Pokemon Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6 min-h-[600px]">
        {loading ? (
          // Loading skeletons
          Array.from({ length: 20 }).map((_, index) => (
            <div key={index} className="space-y-4">
              <Skeleton className="h-48 w-full" />
              <Skeleton className="h-4 w-[250px]" />
            </div>
          ))
        ) : (
          filteredPokemons.map((pokemon) => (
            <Card
              key={pokemon.id}
              className="p-4 cursor-pointer hover:shadow-lg transition-shadow"
              onClick={() => {
                setSelectedPokemon(pokemon)
                setIsDialogOpen(true)
              }}
            >
              <div className="aspect-square relative mb-4">
                <img
                  src={pokemon.sprites?.front_default}
                  alt={pokemon.name}
                  className="w-full h-full object-contain"
                />
              </div>
              <div className="text-center mb-2">
                <span className="text-sm text-gray-500">#{String(pokemon.id).padStart(3, '0')}</span>
                <h2 className="text-xl font-semibold capitalize">
                  {pokemon.name}
                </h2>
              </div>
              <div className="flex gap-2 justify-center">
                {pokemon.types?.map((type) => (
                  <span
                    key={type.type.name}
                    className={`px-2 py-1 rounded-full text-sm text-white ${getTypeColor(type.type.name)}`}
                  >
                    {type.type.name}
                  </span>
                ))}
              </div>
            </Card>
          ))
        )}
      </div>

      {/* Pagination */}
      <div className="flex justify-center items-center gap-2 mt-8 flex-wrap">
        <button
          onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
          disabled={currentPage === 1 || loading}
          className="px-4 py-2 bg-gray-800 text-white rounded-lg disabled:opacity-50 hover:bg-gray-700 transition-colors"
        >
          Previous
        </button>
        
        {renderPaginationButtons()}
        
        <button
          onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
          disabled={currentPage === totalPages || loading}
          className="px-4 py-2 bg-gray-800 text-white rounded-lg disabled:opacity-50 hover:bg-gray-700 transition-colors"
        >
          Next
        </button>
      </div>

      {/* Pokemon Details Dialog */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        {selectedPokemon && (
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle className="text-2xl font-bold capitalize">
                {selectedPokemon.name} <span className="text-gray-500">#{String(selectedPokemon.id).padStart(3, '0')}</span>
              </DialogTitle>
              <DialogDescription>
                Detailed information about {selectedPokemon.name} including stats and types
              </DialogDescription>
            </DialogHeader>
            <div className="grid grid-cols-2 gap-8">
              <div className="aspect-square relative">
                <img
                  src={selectedPokemon.sprites?.front_default}
                  alt={selectedPokemon.name}
                  className="w-full h-full object-contain"
                />
              </div>
              <div>
                <h3 className="text-xl font-semibold mb-4">Stats</h3>
                <div className="space-y-2">
                  {selectedPokemon.stats?.map((stat) => (
                    <div key={stat.stat.name} className="space-y-1">
                      <div className="flex justify-between">
                        <span className="capitalize">{stat.stat.name}:</span>
                        <span className="font-semibold">{stat.base_stat}</span>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-2.5">
                        <div
                          className={`${getTypeColor(selectedPokemon.types?.[0].type.name || 'normal')} h-2.5 rounded-full transition-all duration-500`}
                          style={{ width: `${(stat.base_stat / 255) * 100}%` }}
                        />
                      </div>
                    </div>
                  ))}
                </div>
                <div className="mt-6">
                  <h3 className="text-xl font-semibold mb-2">Types</h3>
                  <div className="flex gap-2">
                    {selectedPokemon.types?.map((type) => (
                      <span
                        key={type.type.name}
                        className={`px-3 py-1 rounded-full text-sm text-white ${getTypeColor(type.type.name)}`}
                      >
                        {type.type.name}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </DialogContent>
        )}
      </Dialog>
    </main>
  )
}
